import create from './index.tpl'
import './index.styl'

const component = create({
  name: 'PullRefresh',

  props: {
    refresh: {
      type: Function,
      required: true
    }
  },

  //   
  data() {
    return {
      msg: '', // 
      touchStart: 0, // 
      loading: 0, // 
      isRefresh: 0, // 
      dis: 0 // 
    }
  },

  mounted () {
    this.$nextTick(() => {
      // 
      let container = this.$refs.container
      //  touchstart
      container.addEventListener('touchstart', (e) => {
        if (this.loading) {
          e.preventDefault()
          return
        }
        this.touchStart = e.targetTouches[0].pageY
      })

      //  touchmove
      container.addEventListener('touchmove', (e) => {
        if (!this.touchStart) {
          return
        }

        if (this.loading) {
          e.preventDefault()
          return
        }

        // scrollTop
        let scrollTop = container.scrollTop
        // Y
        if (scrollTop === 0) {
          let touch = e.targetTouches[0]
          this.dis = touch.pageY - this.touchStart
          if (this.dis > 0) {
            e.preventDefault()
            if (this.dis < 88) {
              // 
              container.style.overflow = 'inherit'
              container.style.transform = `translate3d(0px, ${this.dis}px, 0px)`
              if (this.dis > 60) {
                this.msg = ''
                // , isRefresh = 1
                this.isRefresh = 1
              } else {
                this.msg = ''
              }
            }
          }
        }
      })

      //  touchend
      container.addEventListener('touchend', (e) => {
        if (this.dis === 0) {
          return
        }

        if (this.loading) {
          e.preventDefault()
          return
        }

        // , loading
        if (this.isRefresh && this.dis > 0) {
          // , 
          container.style.transform = 'translate3d(0px, 50px, 0px)'
          this.loading = 1
          this.refresh().then(() => {
            // 
            this.isRefresh = 0
            this.loading = 0
            container.scrollTop = 0
            container.style.overflow = 'auto'
            container.style.transform = 'translate3d(0px, 0px, 0px)'
          })
          return
        }

        // , 
        // 
        container.style.overflow = 'auto'
        container.style.transform = 'translate3d(0px, 0px, 0px)'
      })
    })
  }
})

export default {
  install(Vue) {
    Vue.component(component.name, component)
  }
}



